<template>
    <div>
        <Head headback='false' headto='false'  bol2='true' @add='fn' ></Head>
        <div class="content">
            <div class="addlist">
                <ul>
                    <li v-for='(item,index) of addslist' :key='index'>
                        <p>{{item.name}}</p>
                        <p>{{item.tel}}</p>
                        <i v-if="!bool" class="iconfont iconchahao" @click="getqu(index)"></i>
                    </li>
                </ul>
            </div>
            <router-link tag='div' to='/Message' class="newadd">
                <p>新增地址</p>
                <svg t="1566977412998" class="icon " viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="4947" width="200" height="200">
                        <path d="M713.892234 598.646154c-23.630769 0-43.323077-7.876923-59.076923-27.569231L249.153772 137.846154C217.64608 106.338462 221.584542 55.138462 253.092234 23.630769c31.507692-31.507692 82.707692-27.569231 114.215384 3.938462l401.723077 437.169231c31.507692 31.507692 27.569231 82.707692-3.938461 114.215384-11.815385 11.815385-31.507692 19.692308-51.2 19.692308z" fill="#CCCCCC" p-id="4948"></path>
                        <path d="M312.169157 1024c-19.692308 0-39.384615-7.876923-55.138462-23.630769-31.507692-31.507692-35.446154-82.707692-3.938461-114.215385l401.723077-425.353846c31.507692-31.507692 82.707692-35.446154 114.215384-3.938462 31.507692 31.507692 35.446154 82.707692 3.938462 114.215385L371.24608 1000.369231c-15.753846 15.753846-39.384615 23.630769-59.076923 23.630769z" fill="#CCCCCC" p-id="4949"></path>
                </svg>
            </router-link>
        </div>
    </div>
</template>

<script>
import Head from './../header/Head'
export default {
components: {
    Head
},
data () {
    return {
        nams:'',
        tele:'',
        addslist:[],
        bool:true
    }
},
mounted () {
    if(localStorage.getItem('getadd')){
        this.addslist=JSON.parse(localStorage.getItem('getadd'))
    }
},
methods: {
    fn(data){
        this.bool=data
    },
    getqu(index){
        if(localStorage.getItem('getadd')){
        this.addslist=JSON.parse(localStorage.getItem('getadd'))
        this.addslist.splice(index,1)
        localStorage.setItem('getadd',JSON.stringify(this.addslist))
    }
    }
}
}
</script>

<style lang='scss' scoped>
.content{
    width: 100%;
    height: 100%;
    padding-top: 0.88rem;
    .newadd{
        width: 100%;
        height: 0.84rem;
        margin-top: 0.18rem;
        background: white;
        display: flex;
        justify-content: space-between;
        p {
            font-size: 0.3rem;
            line-height: 0.84rem;
            margin-left: 0.18rem;
        }
        .icon {
            width: 0.3rem;
            height: 0.44rem;
            margin-right: 0.18rem;
            margin-top: 0.2rem;
        }
    }
    .addlist {
        width: 100%;
        height: auto;
        background: white;
        margin-top: 0.1rem;
        ul {
            width: 100%;
            height: 100%;
            li {
                width: 7.14rem;
                height: 0.82rem;
                border-bottom: 0.01rem solid #ddd;
                padding: 0.18rem 0.18rem;
                font-size: 0.3rem;
                text-align: left;
                position: relative;
                p {
                    width: 100%;
                    height: 0.41rem;
                    line-height: 0.41rem;
                }
                i {
                    font-size: 0.3rem;
                    position: absolute;
                    top: 0.4rem;left: 6.9rem;
                }
            }
            li:nth-child(1){
                background: #fff8c3;
            }
        }
    }
}
</style>



